.mobile {
  position: fixed;
  top: var(--header-height);
  right: 0;
  width: 100%;
  height: 100%;
  background: var(--bg-f);
  z-index: 90;
  overflow: hidden;
  transition: 0.3s ease;
  max-width: 430px;
  transform: translateX(110%);
  padding: 32px 24px 32px;

  @include  for-tablet{
    display: contents;
  }

  &.active {
    display: flex;
    transform: translateX(0);

    @include  tablet {
      overflow-y: auto;

      .header__nav ul {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  .burger{
    position: absolute;
    z-index: 10;
    top: 20px;
    right: 23px;
  }

  &__box {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    align-items: center;

    @include  for-tablet{
      display: contents;
    }
  }

  @include tablet {
    .header__nav {
      width: 100%;
      flex-direction: column;
      margin-bottom: 15px;
     
      &:only-child{
        flex-grow: 1;
      }

      ul {
        opacity: 0;
        transition: 0.3s ease;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 24px;

        a{
          width: 100%;
          padding: 4px 0;
          justify-content: start;
          font-size: 20px;
          line-height: 120%;

          &.disabled{
            display: none;
          }
        }
      }
    }
  }
}
